<template>
    <div class="wrapper">
        <h1>Редагувати запис: {{work.title}}</h1>
			<form @submit.prevent="update(work.id,item)">
				<div class="row">
					<div class="col-12 col-lg-6">
						<div class="form-group">
						    <label for="title">Назва роботи</label>
						    <input type="text" v-model='item.title' class="form-control" id="title">
						</div>
					</div>
					<!-- /.col-12 col-lg-6 -->
					
					<div class="col-12 col-lg-6">
						<div class="form-group">
					    	<label for="cat">Категорія:</label>
					    	<select class="form-control" id="cat" v-model='item.cat'>
					    		<option disabled selected value="">Вибери категорію</option>
								<option value="sites">Сайти</option>
								<option value="personal">Особисті проекти</option>
							</select>
					  	</div> 
					</div>
					<!-- /.col-12 col-lg-6 -->
					<div class="col-12 col-lg-6">
						<div class="form-group">
					    	<label for="client">Клієнт:</label>
					    	<input type="text" v-model="item.client" class="form-control" id="client">
					  	</div>
					</div>
					<!-- /.col-12 col-lg-4 -->
					<div class="col-12 col-lg-6">
						<div class="form-group">
					    	<label for="link">Посилання на роботу:</label>
					    	<input type="text" v-model="item.url" class="form-control" id="link">
					  	</div>
					</div>
					<!-- /.col-12 col-lg-4 -->
					<div class="col-12 col-lg-6">
						<div class="form-group">
					    	<label for="tech">Технології:</label>
					    	<input type="text" v-model="item.tech" class="form-control" id="tech">
					  	</div>
					</div>
					<!-- /.col-12 col-lg-4 -->
					<div class="col-12 col-lg-6">
						<div class="form-group">
					    	<label for="deadline">Термін розробки:</label>
					    	<input type="text" v-model="item.deadline" class="form-control" id="deadline">
					  	</div>
					</div>
					<!-- /.col-12 col-lg-6 -->
					<div class="col-12">
						<div class="form-group">
					    	<label for="pre">Прев'ю роботи:</label>
					    	<PreviewImage @getImg='getSrc' :work='work'/>
					  	</div>
					</div>
					<!-- /.col-12 -->
					
				</div>
			  	<div class="form-group">
			    	<label for="describe">Опис роботи:</label>
					<vue-editor for='describe' v-model="item.describe"></vue-editor>
			  	</div>
				
				<button class="btn btn-primary" type="submit">Оновити роботу</button>
			</form>	
			<button class="btn btn-primary" @click='addToStorage(item)'>
		  		<router-link
			      	class="btn btn-primary" 
			      	:to="{ name: 'work_preview', params: { title: $_friendlyUrl(item.title), item }}"
			     >
			      	Попередній перегляд
			    </router-link>	
			</button>	
    </div>
</template>
<script>
	import { db } from '@/main';

	import { VueEditor } from 'vue2-editor';
	const PreviewImage = () => import('@/components/PreviewImage');
	export default {
		name: 'FormWork',
        props: ['work', 'proId'],
		data() {
			return {
				item: JSON.parse(localStorage.getItem('workItem')) || {
					title: this.work.title,
					img: this.work.img,
					cat: this.work.cat,
					describe: this.work.describe,
					deadline: this.work.deadline,
					client: this.work.client,
					url: this.work.url,
					tech: this.work.tech
				},			
			}
		},
		components: {
		      VueEditor,
		      PreviewImage
		   },
		methods: {
		    update(id,item) {
                const createdAt = new Date();
		        db.collection('works').doc(id).update({ ...item, createdAt });
		       	localStorage.removeItem('workItem');
		        	localStorage.removeItem('workId');
		   },
		    getSrc (data) {
		        this.item.img = data;
		    },
		    addToStorage(item) {
		   		localStorage.setItem('workItem', JSON.stringify(item));
		   		localStorage.setItem('workId', JSON.stringify(this.proId));
		   	},
		 }
	}
</script>
<style scoped>
	input, select, textarea {
		display: block;
	}
</style>